'use client';

import { useState, useEffect } from 'react';
import { useRouter, useParams } from 'next/navigation';
import { useAuth } from '@/context/AuthContext';
import Link from 'next/link';
import Image from 'next/image';

export default function PropertyDetailsClient({ initialProperty }) {
  const router = useRouter();
  const params = useParams();
  const { user } = useAuth();
  const [property, setProperty] = useState(initialProperty);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');
  const [currentImageIndex, setCurrentImageIndex] = useState(0);

  useEffect(() => {
    if (params.id && !initialProperty) {
      fetchProperty();
    }
  }, [params.id]);

  const fetchProperty = async () => {
    try {
      setLoading(true);
      setError('');
      const res = await fetch(`/api/properties/${params.id}`);
      const data = await res.json();

      if (!res.ok) {
        throw new Error(data.message || 'Failed to fetch property');
      }

      if (!data.property) {
        throw new Error('Property not found');
      }

      setProperty(data.property);
    } catch (error) {
      console.error('Error fetching property:', error);
      setError(error.message || 'Error fetching property');
    } finally {
      setLoading(false);
    }
  };

  const handleDelete = async () => {
    if (!confirm('Are you sure you want to delete this property?')) {
      return;
    }

    try {
      const res = await fetch(`/api/properties/${params.id}`, {
        method: 'DELETE',
        credentials: 'include',
      });

      const data = await res.json();

      if (!res.ok) {
        throw new Error(data.message || 'Failed to delete property');
      }

      router.push('/properties');
    } catch (error) {
      console.error('Error deleting property:', error);
      setError(error.message || 'Error deleting property');
    }
  };

  const nextImage = () => {
    if (property?.images?.length > 1) {
      setCurrentImageIndex((prev) => 
        prev === property.images.length - 1 ? 0 : prev + 1
      );
    }
  };

  const prevImage = () => {
    if (property?.images?.length > 1) {
      setCurrentImageIndex((prev) => 
        prev === 0 ? property.images.length - 1 : prev - 1
      );
    }
  };

  if (loading) {
    return (
      <div className="container mx-auto px-4 py-8">
        <div className="text-center">Loading property details...</div>
      </div>
    );
  }

  if (error) {
    return (
      <div className="container mx-auto px-4 py-8">
        <div className="text-center text-red-500">{error}</div>
      </div>
    );
  }

  if (!property) {
    return (
      <div className="container mx-auto px-4 py-8">
        <div className="text-center">Property not found</div>
      </div>
    );
  }

  return (
    <div className="container mx-auto px-4 py-8">
      {/* Image Carousel */}
      <div className="relative mb-8 h-[500px] bg-gray-200 rounded-lg overflow-hidden">
        {property.images && property.images.length > 0 ? (
          <>
            <Image
              src={property.images[currentImageIndex]}
              alt={`Property image ${currentImageIndex + 1}`}
              fill
              className="object-cover"
              priority={currentImageIndex === 0}
            />
            {property.images.length > 1 && (
              <>
                <button
                  onClick={prevImage}
                  className="absolute left-4 top-1/2 transform -translate-y-1/2 bg-black bg-opacity-50 text-white p-2 rounded-full hover:bg-opacity-75"
                >
                  ←
                </button>
                <button
                  onClick={nextImage}
                  className="absolute right-4 top-1/2 transform -translate-y-1/2 bg-black bg-opacity-50 text-white p-2 rounded-full hover:bg-opacity-75"
                >
                  →
                </button>
                <div className="absolute bottom-4 left-1/2 transform -translate-x-1/2 flex space-x-2">
                  {property.images.map((_, index) => (
                    <button
                      key={index}
                      onClick={() => setCurrentImageIndex(index)}
                      className={`w-3 h-3 rounded-full ${
                        currentImageIndex === index ? 'bg-white' : 'bg-white/50'
                      }`}
                    />
                  ))}
                </div>
              </>
            )}
          </>
        ) : (
          <div className="flex items-center justify-center h-full">
            <p>No images available</p>
          </div>
        )}
      </div>

      {/* Property Details */}
      <div className="grid grid-cols-1 md:grid-cols-3 gap-8">
        <div className="md:col-span-2">
          <div className="bg-white rounded-lg shadow-md p-6">
            <div className="flex justify-between items-start mb-6">
              <div>
                <h1 className="text-3xl font-bold mb-2">{property.title}</h1>
                <p className="text-gray-600 text-xl mb-2">{property.location}</p>
                <p className="text-2xl font-bold text-blue-600">
                  ${Number(property.price).toLocaleString()}
                </p>
              </div>
              {user && user._id === property.owner._id && (
                <div className="flex space-x-2">
                  <Link
                    href={`/properties/edit/${property._id}`}
                    className="bg-blue-500 text-white px-4 py-2 rounded-md hover:bg-blue-600"
                  >
                    Edit
                  </Link>
                  <button
                    onClick={handleDelete}
                    className="bg-red-500 text-white px-4 py-2 rounded-md hover:bg-red-600"
                  >
                    Delete
                  </button>
                </div>
              )}
            </div>

            <div className="mb-6">
              <h2 className="text-xl font-semibold mb-2">Description</h2>
              <p className="text-gray-700">{property.description}</p>
            </div>

            <div className="grid grid-cols-2 gap-4 mb-6">
              <div>
                <h2 className="text-xl font-semibold mb-2">Details</h2>
                <ul className="space-y-2">
                  <li className="flex items-center">
                    <span className="font-medium mr-2">Property Type:</span>
                    {property.propertyType}
                  </li>
                  <li className="flex items-center">
                    <span className="font-medium mr-2">Status:</span>
                    {property.status}
                  </li>
                  <li className="flex items-center">
                    <span className="font-medium mr-2">Area:</span>
                    {property.features?.area} sq ft
                  </li>
                </ul>
              </div>
              <div>
                <h2 className="text-xl font-semibold mb-2">Features</h2>
                <ul className="space-y-2">
                  <li className="flex items-center">
                    <span className="font-medium mr-2">Bedrooms:</span>
                    {property.features?.bedrooms}
                  </li>
                  <li className="flex items-center">
                    <span className="font-medium mr-2">Bathrooms:</span>
                    {property.features?.bathrooms}
                  </li>
                  <li className="flex items-center">
                    <span className="font-medium mr-2">Parking:</span>
                    {property.features?.parking ? 'Yes' : 'No'}
                  </li>
                  <li className="flex items-center">
                    <span className="font-medium mr-2">Furnished:</span>
                    {property.features?.furnished ? 'Yes' : 'No'}
                  </li>
                </ul>
              </div>
            </div>

            <div>
              <h2 className="text-xl font-semibold mb-2">Location</h2>
              <div className="bg-gray-100 p-4 rounded-lg">
                <p className="mb-1">{property.address?.street}</p>
                <p className="mb-1">
                  {property.address?.city}, {property.address?.state} {property.address?.zipCode}
                </p>
                <p>{property.address?.country}</p>
              </div>
            </div>
          </div>
        </div>

        {/* Contact Section */}
        <div className="md:col-span-1">
          <div className="bg-white rounded-lg shadow-md p-6 sticky top-4">
            <h2 className="text-xl font-semibold mb-4">Contact Agent</h2>
            {user ? (
              <form className="space-y-4">
                <div>
                  <label className="block text-sm font-medium text-gray-700">
                    Message
                  </label>
                  <textarea
                    className="mt-1 block w-full rounded-md border-gray-300 shadow-sm focus:border-blue-500 focus:ring-blue-500"
                    rows="4"
                    placeholder="I'm interested in this property..."
                  ></textarea>
                </div>
                <button
                  type="submit"
                  className="w-full bg-blue-500 text-white px-4 py-2 rounded-md hover:bg-blue-600"
                >
                  Send Message
                </button>
              </form>
            ) : (
              <div className="text-center">
                <p className="mb-4">Please login to contact the agent</p>
                <Link
                  href="/login"
                  className="block w-full bg-blue-500 text-white px-4 py-2 rounded-md hover:bg-blue-600"
                >
                  Login
                </Link>
              </div>
            )}
          </div>
        </div>
      </div>
    </div>
  );
}

import PropertyDetailsClient from './PropertyDetailsClient';
import connectDB from '@/lib/db';
import Property from '@/models/property';

async function getProperty(id) {
  try {
    await connectDB();
    const property = await Property.findById(id)
      .populate('owner', 'name email');
    
    if (!property) {
      throw new Error('Property not found');
    }

    return JSON.parse(JSON.stringify(property));
  } catch (error) {
    console.error('Error fetching property:', error);
    throw error;
  }
}

export default async function PropertyDetailsPage({ params }) {
  try {
    const property = await getProperty(params.id);
    return <PropertyDetailsClient initialProperty={property} />;
  } catch (error) {
    return (
      <div className="container mx-auto px-4 py-8">
        <div className="text-center text-red-500">
          {error.message || 'Error loading property'}
        </div>
      </div>
    );
  }
}
